import React, { forwardRef } from 'react';
import { useMatchMedia } from '../../mq/modern';
import { InputDesktop } from './desktop/Component.desktop.js';
import { InputMobile } from './mobile/Component.mobile.js';
import '../../form-control/modern/desktop';
import './components/base-input/Component.js';
import 'react-merge-refs';
import 'classnames';
import '../../badge/modern';
import '../../shared/modern';
import '@alfalab/hooks';
import '@alfalab/icons-glyph/CheckmarkCircleMIcon';
import '@alfalab/icons-glyph/ExclamationCircleMIcon';
import './components/clear-button/Component.js';
import '../../button/modern';
import '@alfalab/icons-glyph/CrossCircleMIcon';
import '../../form-control/modern/mobile';

const Input = forwardRef(({ breakpoint = 1024, defaultMatchMediaValue, ...restProps }, ref) => {
    const query = `(min-width: ${breakpoint}px)`;
    const [isDesktop] = useMatchMedia(query, defaultMatchMediaValue);
    const Component = isDesktop ? InputDesktop : InputMobile;
    return React.createElement(Component, { ref: ref, ...restProps });
});

export { Input };
