import React, { forwardRef } from 'react';
import { useMatchMedia } from '../../mq/modern';
import { ButtonDesktop } from './desktop/Component.desktop.js';
import { ButtonMobile } from './mobile/Component.mobile.js';
import './components/base-button/Component.js';
import 'react-merge-refs';
import 'classnames';
import '../../shared/modern';
import '../../spinner/modern';
import '@alfalab/hooks';

const Button = forwardRef(({ children, breakpoint = 1024, ...restProps }, ref) => {
    const query = `(min-width: ${breakpoint}px)`;
    const [isDesktop] = useMatchMedia(query);
    const Component = isDesktop ? ButtonDesktop : ButtonMobile;
    return (React.createElement(Component, { ref: ref, ...restProps }, children));
});

export { Button };
