const TIMEOUT = 500;
const SWIPE_VELOCITY = 0.4;
const MARKER_HEIGHT = 24;
const CLOSE_OFFSET = 0.2;
const SCROLL_OFFSET = 1;
const HEADER_OFFSET = 24;
const convertPercentToNumber = (value, fullHeight, headerOffset = HEADER_OFFSET) => {
    const maxHeight = fullHeight - headerOffset;
    if (typeof value === 'string') {
        const percent = parseFloat(value) / 100;
        if (percent < 0) {
            return Math.min(maxHeight, fullHeight + fullHeight * percent);
        }
        return Math.min(maxHeight, fullHeight * percent);
    }
    if (value < 0) {
        return Math.max(0, fullHeight + value);
    }
    return Math.min(maxHeight, value);
};

export { CLOSE_OFFSET, HEADER_OFFSET, MARKER_HEIGHT, SCROLL_OFFSET, SWIPE_VELOCITY, TIMEOUT, convertPercentToNumber };
