import React, { forwardRef } from 'react';
import { useMatchMedia } from '../../mq/modern';
import { ToastDesktop } from './desktop/Component.desktop.js';
import { ToastMobile } from './mobile/Component.mobile.js';
import '../../toast-plate/modern/desktop';
import './components/base-toast/component.js';
import 'react-merge-refs';
import 'react-transition-group';
import 'classnames';
import '../../popover/modern';
import '../../portal/modern';
import '../../stack/modern';
import '@alfalab/hooks';
import '../../toast-plate/modern/mobile';

const Toast = forwardRef(({ children, breakpoint = 1024, ...restProps }, ref) => {
    const query = `(min-width: ${breakpoint}px)`;
    const [isDesktop] = useMatchMedia(query);
    const Component = isDesktop ? ToastDesktop : ToastMobile;
    return (React.createElement(Component, { ref: ref, ...restProps }, children));
});

export { Toast };
